body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  position: relative;
  width: 100%;
}

.App-header {
  background-color: white;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
}

.backgoundImg {
  width: 70%;
  padding-left: 30%;
}

.sideCircle {
  width: 60%;
  margin-top: -29%;
  margin-left: -20%;
  opacity: 0.7;
}

.App-link {
  color: #61dafb;
}

a:visited {
  text-decoration: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Navbar {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  position: absolute;
  font-size: calc(8px + 2vmin);
}

.home {
  font-size: calc(18px + 2vmin);
  font-weight: bold;
  padding-left: 4%;
  margin-top: -1%;
}

.tabs {
  display: -webkit-flex;
  display: flex;
  padding-left: 43%;
}

a {
  padding-left: 8%;
  display: -webkit-flex;
  display: flex;
  width: 40%;
  text-decoration: none;
  color: black;
}

.loginIcon {
  width: 30%;
  margin-top: -20%;
  display: -webkit-inline-flex;
  display: inline-flex;
  padding-left: 5%;
}

.Schools {
  width: 80%;
  height: 100vh;
  background-color: white;
  margin-left: -88%;
  margin-top: 5%;
  position: static;
  z-index: 1;
}

